<template>
  <div>
    <div class="iems-dashboard">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pb-6" type="primary"></base-header>

      <div class="container-fluid mt--5">
        <div class="row">
          <div class="col">
            <div class="card border-0">
              <div
                id="map-custom"
                class="map-canvas"
                style="height: calc(100vh - 512px); min-height: 350px"
              ></div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-lg-6 col-12">
            <div class="card border-0">
              <div class="card-header">
                <div class="row align-items-center">
                  <div class="col-6">
                    <h2 class="m-0">Pemenuhan RKL</h2>
                  </div>
                  <div class="col-6 text-right">
                    <el-select
                      size="mini"
                      v-model="periodeRKL"
                      placeholder="Pilih Periode"
                      @change="
                        changePeriodeRKL(periodeRKL);
                        changePeriodeRPL(periodeRKL);
                      "
                    >
                      <el-option
                        v-for="option in listPeriode"
                        :key="option.label"
                        :label="option.label"
                        :value="option.value"
                      ></el-option>
                    </el-select>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="dashboard card-body overflow-auto">
                <div class="col">
                  <div
                    class="row"
                    v-for="index in dataPemenuhanRKL.length"
                    :key="index"
                  >
                    <div class="col-10 pl-0">
                      <h3 class="title text-muted">
                        {{ dataPemenuhanRKL[index - 1].name }}
                      </h3>
                      <base-progress
                        :height="10"
                        type="danger"
                        :value="
                          (dataPemenuhanRKL[index - 1].rklRep /
                            dataPemenuhanRKL[index - 1].rkl) *
                          100
                            ? (dataPemenuhanRKL[index - 1].rklRep /
                                dataPemenuhanRKL[index - 1].rkl) *
                              100
                            : 0
                        "
                      ></base-progress>
                    </div>
                    <div class="col-2 p-0">
                      <div
                        class="container-fraction bg-gray d-flex align-items-center justify-content-center text-align-center h-100 w-100 text-white"
                      >
                        <p class="m-0">
                          {{ dataPemenuhanRKL[index - 1].rklRep }}/{{
                            dataPemenuhanRKL[index - 1].rkl
                          }}
                        </p>
                      </div>
                    </div>
                    <div
                      class="col-12"
                      style="
                        border-top: 2px solid lightgray;
                        margin: 30px auto 15px auto;
                        padding: 10;
                      "
                    ></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-6 col-12">
            <div class="card border-0">
              <div class="card-header">
                <div class="row align-items-center">
                  <div class="col-6">
                    <h2 class="m-0">Pemenuhan RPL</h2>
                  </div>
                  <div class="col-6 text-right">
                    <el-select
                      size="mini"
                      v-model="periodeRKL"
                      placeholder="Pilih Periode"
                      @change="
                        changePeriodeRKL(periodeRKL);
                        changePeriodeRPL(periodeRKL);
                      "
                    >
                      <el-option
                        v-for="option in listPeriode"
                        :key="option.label"
                        :label="option.label"
                        :value="option.value"
                      ></el-option>
                    </el-select>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="dashboard card-body overflow-auto">
                <div class="col">
                  <div
                    class="row"
                    v-for="index in dataPemenuhanRPL.length"
                    :key="index"
                  >
                    <div class="col-10 pl-0">
                      <h3 class="title text-muted">
                        {{ dataPemenuhanRPL[index - 1].name }}
                      </h3>
                      <base-progress
                        :height="10"
                        type="danger"
                        :value="
                          (dataPemenuhanRPL[index - 1].rplRep /
                            dataPemenuhanRPL[index - 1].rpl) *
                          100
                            ? (dataPemenuhanRPL[index - 1].rplRep /
                                dataPemenuhanRPL[index - 1].rpl) *
                              100
                            : 0
                        "
                      ></base-progress>
                    </div>
                    <div class="col-2 p-0">
                      <div
                        class="container-fraction bg-gray d-flex align-items-center justify-content-center text-align-center h-100 w-100 text-white"
                      >
                        <p class="m-0">
                          {{ dataPemenuhanRPL[index - 1].rplRep }}/{{
                            dataPemenuhanRPL[index - 1].rpl
                          }}
                        </p>
                      </div>
                    </div>
                    <div
                      class="col-12"
                      style="
                        border-top: 2px solid lightgray;
                        margin: 30px auto 15px auto;
                        padding: 10;
                      "
                    ></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Select, Option } from "element-ui";
import BaseButton from "@/components/BaseButton";
import BaseProgress from "@/components/BaseProgress";
import GoogleMapsLoader from "google-maps";
import markerContent from "./marker.js";
import { API_KEY } from "@/views/Maps/API_KEY.js";
GoogleMapsLoader.KEY = API_KEY;
import axios from "axios";
// import listPeriode from "@/views/Pages/listPeriode.js";

export default {
  components: {
    BaseProgress,
    BaseButton,
    [Select.name]: Select,
    [Option.name]: Option
  },
  methods: {
    sortList(a, b) {
      // Use toUpperCase() to ignore character casing
      const nameA = a.name.toUpperCase();
      const nameB = b.name.toUpperCase();

      let comparison = 0;
      if (nameA > nameB) {
        comparison = 1;
      } else if (nameA < nameB) {
        comparison = -1;
      }
      return comparison;
    },
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then(res => {
          console.log(res.data);
          let tempData = [];
          res.data.forEach(item => {
            tempData.push({
              label: item.PERIODE,
              value: item.PERIODE
            });
          });
          tempData.reverse();
          this.listPeriode = tempData;
          this.periodeRPL = this.listPeriode[0].value;
          this.periodeRKL = this.listPeriode[0].value;
          this.changePeriodeRKL(this.listPeriode[0].value);
          this.changePeriodeRPL(this.listPeriode[0].value);
        })
        .catch(err => {
          console.log(err);
        });
    },

    changePeriodeRPL(id) {
      axios
        .get("http://45.127.133.75:3000/api/iems/dash-rpl/" + id)
        .then(res => {
          console.log(res.data);
          this.dataPemenuhanRPL = res.data.sort(this.sortList);
        })
        .catch(err => {
          console.log(err);
        });
    },

    changePeriodeRKL(id) {
      axios
        .get("http://45.127.133.75:3000/api/iems/dash-rkl/" + id)
        .then(res => {
          console.log(res.data);
          this.dataPemenuhanRKL = res.data.sort(this.sortList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getPemenuhanMap(ukname) {
      // this.getPemenuhan();
      let dataRKL = this.dataPemenuhanRKL;
      let dataRPL = this.dataPemenuhanRPL;
      const arrayRKL = dataRKL.filter(item => {
        return item.name == ukname;
      });
      const arrayRPL = dataRPL.filter(item => {
        return item.name == ukname;
      });
      let stringPemenuhan =
        "<p class='mx-auto'>RPL : " +
        arrayRKL[0].rklRep +
        "/" +
        arrayRKL[0].rkl +
        ", RPL: " +
        arrayRPL[0].rplRep +
        "/" +
        arrayRPL[0].rpl +
        "</p></div>";
      return stringPemenuhan;
    },
    getPemenuhan() {
      axios
        .get("http://45.127.133.75:3000/api/iems/dash")
        .then(res => {
          this.dataPemenuhanRKL = res.data.rkl.sort(this.sortList);
          this.dataPemenuhanRPL = res.data.rpl.sort(this.sortList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getMap() {
      axios.get("http://45.127.133.75:3000/api/iems/map").then(res => {
        this.mapData = res.data;
        GoogleMapsLoader.load(google => {
          this.initMap(google);
        });
      });
    },
    initMap(google) {
      let map,
        lat = 0.340199,
        lng = 116.472123,
        color = "#5e72e4";
      map = document.getElementById("map-custom");

      let centerLat = new google.maps.LatLng(0.340199, 116.472123);
      let mapOptions = {
        zoom: 5,
        scrollwheel: false,
        center: centerLat,
        mapTypeId: google.maps.MapTypeId.ROADMAP
      };

      map = new google.maps.Map(map, mapOptions);
      var infowindow = null;
      for (let i = 0; i < this.mapData.length; i++) {
        let pos = new google.maps.LatLng(
          this.mapData[i].UK_LAT,
          this.mapData[i].UK_LONG
        );
        let url = "http://maps.google.com/mapfiles/ms/icons/orange-dot.png";
        let blueDot = "http://maps.google.com/mapfiles/ms/icons/blue-dot.png";

        // url += markerContent[i].icon;
        let marker = [];
        marker[i] = new google.maps.Marker({
          icon: { url: url },
          position: pos,
          map: map,
          animation: google.maps.Animation.DROP,
          title: this.mapData[i].UK_NAME
        });
        const that = this;
        google.maps.event.addListener(marker[i], "click", function() {
          let content_ =
            '<div class="fs-16 mx-auto info-window-content"><h2>' +
            that.mapData[i].UK_NAME +
            "</h2>" +
            that.getPemenuhanMap(that.mapData[i].UK_NAME);
          if (infowindow) {
            infowindow.close();
          }
          infowindow = new google.maps.InfoWindow({
            content: content_
          });
          infowindow.open(map, marker[i]);
        });
      }
    }
  },
  created() {
    // this.getPemenuhan();
    this.getListPeriode();
  },
  mounted() {
    this.getMap();
  },
  data() {
    return {
      periodeRKL: "",
      periodeRPL: "",
      listPeriode: [],
      dataPemenuhanRKL: [],
      dataPemenuhanRPL: [],
      mapData: []
    };
  }
};
</script>

